<template>
    <field-messages show="$dirty && ($focused || $touched)" :name="name">
        <div v-for="item in fields" :slot="item.name"><span v-text="showMsg[item.name]?showMsg[item.name]:item.message" v-if="showItem===item.name"></span>
            
        </div>
    </field-messages>
</template>
<script>
    export default {
        data() {
            return {
                fields: [{
                    name: 'required',
                    message: this.$t('error.required')
                }, {
                    name: 'rePassword',
                    message: this.$t('error.rePassword')
                }, {
                    name: 'max',
                    message: this.$t('error.max')
                }]
            }
        },
        props: {
            name: {
                type: String
            },
            itemStatus: {
                type: Object,
                default: () => {
                    return {}
                }
            },
            showMsg: {
                type: Object,
                default: () => {
                    return {}
                }
            }
        },
        computed: {
            showItem() {
                if (this.itemStatus && this.itemStatus.$error) {
                    let str = ''
                    this.fields.some( field => {
                        if(this.itemStatus.$error.hasOwnProperty(field.name)) {
                            str = field.name
                            return true
                        }
                    })
                    return str
                }
            }
        }
    }
</script>
<i18n>
    {
        "zh": {
            "error": {
                "required": "此项为必填项",
                "rePassword": "两次密码输入不一致",
                "max": "123"
            }
        }
    }
</i18n>
<style scoped>
    span {
        color: red;
    }
</style>


