export default {
    get(http) {
        return {
            //*************************************用户相关***********************************************
            user:{
                //根据角色查询用户
                queryUsersByRoleType(role_type) {
                    return http.get('/homework/users?role_type=' + role_type)
                },
                //增加用户
                addUser(user) {
                    return http.post('/keystone/users', user)
                },
                //修改用户
                modifyUser(user){
                    return http.put('/homework/users', user)
                },
                //学生申请加入班级
                applyForJoinClass(userId, classId){
                    return http.post('/homework/users/applyForClass?userId=' + userId + '&classId=' + classId)
                },
                //变更班级申请
                applyForChangeClass(classId){
                    return http.put('/homework/users/changeClass?classId=' + classId)
                },
                //查询同班学生
                queryUsersByClassmates(){
                    return http.get('/homework/users/classmates')
                },
                //班长注册
                applyForMonitor(inviteCode){
                    return http.get('/homework/users/isMonitor?inviteCode=' + inviteCode)
                },
                //班长踢人出班级
                kickOutStudent(handleUser){
                    return http.put('/homework/users/kickOut', handleUser)
                },
                //学生注册
                register(registrar){
                    return http.post('/homework/users/register', registrar)
                },
                //查找所有当前登录用户为处理人的注册申请
                queryAllRegistrars(){
                    return http.get('/homework/users/registrars')
                },
                //处理注册申请
                handleUserRegister(handleUser){
                    return http.put('/homework/users/registrars/check', handleUser)
                },
                //查找当前用户待处理的注册申请
                queryUnhandledRegistrars(){
                    return http.get('/homework/users/unhandledRegistrars')
                },
                //批量删除用户
                removeUsers(idList){
                  return http.delete('/homework/users/' + idList)
                },
                //查询单个用户
                getUserById(id) {
                    return http.get('/homework/users/' + id)
                },
                //重置用户密码
                resetPassword(id) {
                    return http.put('/keystone/users/reset_password?id=' + id)
                }
            },
        //    **********************************************个人网盘相关**********************************************
            netDisk:{
                //复制文件
                copyFiles(copyInfo){
                    return http.post('/netdisk/copyFile', copyInfo)
                },
                //在某个目录下创建空文件夹
                createFolder(path){
                    return http.post('/netdisk/createFolder?path=' + path)
                },
                //下载单个文件/文件夹
                downloadFile(path){
                    return window.location.origin + "/athena/netdisk/downloadFile?path=" + path
                    // return "http://192.168.3.210:8883/netdisk/downloadFile?path=" + path
                },
                //查询文件列表
                queryFilesByPath(path){
                    return http.get('/netdisk/listFiles?path=' + path)
                },
                //查询所有文件，用于复制/移动文件
                queryAllFiles(){
                    return http.get('/netdisk/listFiles/tree')
                },
                //文件分类查询
                queryFilesByType(type){
                    return http.get('/netdisk/listFiles/'+ type)
                },
                //移动文件
                moveFile(moveInfo){
                    return http.post('/netdisk/moveFile', moveInfo)
                },
                //查询当前登录用户个人网盘使用情况
                getQuotaUse(){
                  return http.get('/netdisk/quota')
                },
                //批量删除文件
                removeFiles(path){
                    return http.delete('/netdisk/removeFile?path=' + path)
                },
                //查询分享的文件
                queryFilesBySharedWith(shared_with_me){
                    return http.get('/netdisk/shareFile?shared_with_me=' + shared_with_me)
                },
                //分享文件给多个用户
                shareFile(shareInfo){
                    return http.post('/netdisk/shareFile', shareInfo)
                },
                //批量取消分享
                cancelShares(shareIds){
                    return http.delete('/netdisk/shareFile/' + shareIds)
                },
                //TODO 上传文件
                uploadFile(file, path){
                    return http.post('/netdisk/uploadFile?path=' + path, file)
                }
            },
        //    **************************************课程组相关********************************************
            lessonGroup: {
                //创建课程组
                createLessonGroup(lessonGroup){
                    return http.post('/homework/groups', lessonGroup)
                },
                //修改课程组
                modifyLessonGroup(lessonGroup){
                    return http.put('/homework/groups', lessonGroup)
                },
                //查询当前登录用户已加入或者拥有的课程组
                queryLessonGroupsByCurrentUser(){
                    return http.get('/homework/groups/my')
                },
                //查询当前登陆用户未加入的课程组
                queryLessonGroupByNotIn(){
                    return http.get('/homework/groups/my/not_in')
                },
                //查询所有申请
                queryAllApply(groupId){
                    return http.get('/homework/groups/my/' + groupId)
                },
                //查询待处理的申请
                queryApplyUnchecked(groupId){
                    return http.get('/homework/groups/my/' + groupId + '/unchecked')
                },
                //处理加入课程组申请
                handleApply(ids, event){
                    return http.put('/homework/groups/my/' + ids + '/check/' + event)
                },
                //从课程组中移除当前登录用户
                removeMeOutGroups(lesson_group_ids){
                    return http.delete('/homework/groups/my/' + lesson_group_ids)
                },
                //当前登录用户申请加入课程组
                addMeToGroups(lesson_group_ids){
                    return http.post('/homework/groups/my/' + lesson_group_ids)
                },
                //查询课程组及其学生、老师、未加入组的学生，用于文件分享
                queryLessonGroupAndUsers(){
                    return http.get('/homework/groups/with_users')
                },
                //批量删除课程组
                removeLessonGroups(ids){
                    return http.delete('/homework/groups/' + ids)
                },
                //根据id查询单个课程组
                getLessonGroupById(id){
                    return http.get('/homework/groups/' + id)
                },
                //根据课程组id查询学生
                queryUsersByLessonGroupId(lesson_group_id){
                    return http.get('/homework/groups/' + lesson_group_id + '/users')
                },
                //从课程组批量移除学生
                removeUsersOutLessonGroup(lesson_group_id, user_ids){
                    return http.delete('/homework/groups/' + lesson_group_id + '/users/' + user_ids)
                },
                //批量添加学生到课程组
                addUsersToLessonGroup(lesson_group_id, user_ids){
                    return http.post('/homework/groups/' + lesson_group_id + '/users/' + user_ids)
                }
            },
        //    ***************************************登陆、注销*************************************************
            login:{
                //登陆,TODO response怎么传
                login(loginInfo){
                    return http.post('/login', loginInfo)
                },
                getCurrentLoginUser(){
                    return http.get('/current_login_user')
                },
                refreshToken(){
                    return http.get('/refresh_token')
                }
            },
        //    *****************************************作业模板*******************************************************
            templates:{
                //根据当前登陆用户查询作业模板
                queryTemplatesByCurrentUser(){
                    return http.get('/homework/templates')
                },
                //新增作业模板
                createTemplate(template){
                    return http.post('/homework/templates', template)
                },
                //修改作业模板
                modifyTemplate(template){
                    return http.put('/homework/templates', template)
                },
                //查询所有作业模板
                queryAllTemplate(){
                    return http.get('/homework/templates/all')
                },
                //批量删除作业模板
                removeTemplates(ids){
                    return http.delete('/homework/templates/' + ids)
                },
                //根据id查询作业模板
                getTemplateById(id){
                    return http.get('/homework/templates/' + id)
                }
            },
        //    ******************************************参数配置**************************************************8
            configServer:{
                //查询配置
                queryConfig(){
                    return http.get('/config')
                },
                //更新配置
                modifyConfig(configInfo){
                    return http.put('/config', configInfo)
                },
                //获取所有可用分区列表
                queryDisks(){
                    return http.get('/config/mount/paths')
                },
                //修改个人网盘配额
                modifyQuota(quota){
                    return http.put('/config/quota/' + quota)
                },
                //修改皮肤设置
                modifySkin(skinInfo){
                    return http.put('/config/skin', skinInfo)
                },
                //查询皮肤设置
                getSkinConfig(username){
                    return http.get('/config/skin/' + username)
                }
            },
        //   *********************************************归档************************************************
            archive:{
                //归档
                archive(year){
                    return http.put('/homework/archive/' + year)
                }
            },
        //    ********************************************附件******************************************************
            attachment:{
                //下载附件
                downloadAttachment(attachment){
                    return http.post('/homework/attachments/attachment_download', attachment)
                },
                //上传附件。TODO 大师确认下是不是这么写
                uploadAttachment(file){
                    return http.post('/homework/attachments/attachment_upload', file)
                }
            },
        //    *********************************************班级***********************************************************
            classManagement:{
                //查询所有班级
                queryAllClasses(){
                    return http.get('/homework/classEntity')
                },
                //添加班级
                createClass(classEntity){
                    return http.post('/homework/classEntity', classEntity)
                },
                //修改班级
                modifyClass(classEntity){
                    return http.put('/homework/classEntity', classEntity)
                },
                //根据id查询班级信息
                getClassById(collegeId){
                    return http.get('/homework/classEntity/' + collegeId)
                },
                //批量删除班级
                removeClasses(idList){
                    return http.delete('/homework/classEntity/' + idList)
                },
                //重置班级的邀请码
                resetInviteCode(classId){
                    return http.put('/resetInviteCode/' + classId)
                }
            },
        //    *************************************************点赞相关******************************************************
            praise:{
                //根据id查询
                getPraiseById(praiseId){
                    return http.get('/homework/homeworkPrasie/' + praiseId)
                },
                //删除点赞数据
                removePraise(praiseId, userId){
                    return http.delete('/homework/homeworkPrasie/' + praiseId + "/" + userId)
                },
                //添加点赞
                createPraise(praiseId, userId){
                    return http.post('/homework/homeworkPrasie/' + praiseId + "/" + userId)
                }
            },
        //    ************************************************老师作业相关*************************************************************
            homework:{
                //根据当前登陆用户查询作业
                queryHomeworks(){
                    return http.get('/homework/homeworks')
                },
                //添加作业
                createHomework(homework){
                    return http.post('/homework/homeworks', homework)
                },
                //修改作业
                modifyHomework(homework){
                    return http.put('/homework/homeworks', homework)
                },
                //根据课程组id查询作业
                queryHomeworksByGroupId(groupId){
                    return http.get('/homework/homeworks/lessonGroup/' + groupId)
                },
                //根据课程组id查询展示作业
                queryShowedHomeworksByGroupId(groupId){
                    return http.get('/homework/homeworks/showed_homeworks/lessonGroup/' + groupId)
                },
                //根据老师作业id查询展示作业
                queryShowedHomeworksByHomeworkId(homeworkId){
                    return http.get('/homework/homeworks/showed_homeworks/' + homeworkId)
                },
                //给作业打分
                setScore(homeworkSub){
                    return http.put('/homework/homeworks/submitted_works', homeworkSub)
                },
                //修改是否展示
                modifyShowStatus(showHomeworkInfo){
                    return http.put('/homework/homeworks/submitted_works/showed', showHomeworkInfo)
                },
                //获取某学生提交作业详情给老师批改
                getHomeworkSubById(homeworkSubId){
                    return http.get('/homework/homeworks/submitted_works/' + homeworkSubId)
                },
                //根据id获取老师作业
                getHomeworkById(homeworkId){
                    return http.get('/homework/homeworks/' + homeworkId)
                },
                //根据id删除作业
                removeHomeworkById(id){
                    return http.put('/homework/homeworks/' + id)
                },
                //根据老师作业id查询所有学生提交的作业
                querySubmittedHomeworksByHomeworkId(homeworkId){
                    return http.get('/homework/homeworks/' + homeworkId + '/submitted_works');
                }
            },
        //    ****************************************学生作业相关********************************************
            stuHomework: {
                //查询当前登录学生的所有作业
                queryAllHomeworks(){

                }
            },
        //    ****************************************学院相关********************************************
            collage: {
                //查询所有学院
                queryAllCollages(){
                    return http.get('/homework/colleges')
                },
                //根据id查询学院
                getCollageById(id) {
                    return http.get('/homework/colleges/' + id)
                },
                //创建学院
                createCollage(collage) {
                    return http.post('/homework/colleges/', collage)
                },
                //删除学院
                deleteCollage(ids) {
                    return http.delete('/homework/colleges/' + ids)
                },
                //修改学院
                updateCollage(collage) {
                    return http.put('/homework/colleges/', collage)
                }
            }
        }
    }
}



