        padding: 0 10px!important;
    }
    label.radio-label {
        display: inline-block;
        margin-top: 8px;
        position: relative;
    }
    .radio-label input[type="radio"] {
        display: none;
    }
    .radio-label .radio-iocn {
        background-color: #fff;
        border-radius: 14px;
        border: 1px solid #cad8df;
        display: inline-block;
        width: 13px;
        height: 13px;
        margin-right: 4px;
    }
    .radio-label input[type="radio"]:checked + .radio-iocn::after {
        background-color: #018fff;
        border-radius: 8px;
        content: "";
        display: inline-block;
        height: 7px;
        width: 7px;
        position: absolute;
        top: 4px;
        left: 4px;
    }
    .m-t-230 {
        margin-top: 230px;
    }
</style>



