const loginUsername = ({loginUser}) => loginUser ? loginUser.username : ''
const navStatus = ({navStatus}) => navStatus
const roleType = ({roleType}) => roleType
const isAdmin = ({isAdmin}) => isAdmin
const isTeacher = ({isTeacher}) => isTeacher
const isStudent = ({isStudent}) => isStudent

export default {
    loginUsername,
    navStatus,
    roleType,
    isAdmin,
    isTeacher,
    isStudent
}



