<template>
    <div style="position: relative">
        <div @click.stop="dropDown" class="pointer">
            <input type="text" :class="selectClass" :placeholder="placeholder" v-model="selectValue" :readonly="!isText" :name="name" :required="required">
            <span :class="[isActive?'select-area-up':isFirst?'':'select-area-down','select-area-caret']"><i class="el-select__caret el-input__icon el-icon-arrow-down"></i></span>
        </div>
        <div class="sa-dropDown-menu" v-show="isActive">
            <ul>
                <li v-for="option in options" @click="confirmSelect(option.value, option.text)">{{option.text?option.text:option.value}}</li>
            </ul>
        </div>
    </div>
</template>
<script>
    function getSelectText(value, array) {
        let res;
        array.some(item => {
            if(item.value === value) {
                res = item.text?item.text:item.value
            }
        })
        return res?res:'';
    }

    export default {
        data() {
            return {
                isFirst: true,
                isActive: false
            }
        },
        methods: {
            dropDown() {
                let isActive = this.isActive
                this.isActive = !isActive
                this.isFirst = false
            },
            confirmSelect(value) {
                this.isActive = false
                this.$emit('getSelectValue', value, this.name)
            }
        },
        mounted() {
            const socpe = this;
            this.globalClick(function() {
                socpe.isActive = false
            })
        },
        props: {
            className: {
                type: String,
                default: 'select-area-input'
            },
            isText: {
                type: Boolean,
                default: false
            },
            options: {
                type: Array,
                default: () => []
            },
            name: {
                type: String
            },
            value: {},
            required: {
                type: Boolean
            },
            placeholder: {
                type: String
            }
        },
        computed: {
            selectClass() {
                const defaultClass = 'pointer select-area-input' + ' ' + this.className
                return this.isActive ? 'pointer select-area-active ' + defaultClass : defaultClass
            },
            selectValue() {
                return getSelectText(this.value, this.options)
            }
        }
    }
</script>
<style scoped>
    .select-area-input {
        width: calc(100% - 25px);
        height: 40px;
        background-color: #ffffff;
        border-radius: 1px;
        padding: 0 20px;
        box-sizing: border-box;
        font-size: 14px;
        border-width: 1px 0px 1px 1px;
        border-color: #cad8df;
        border-style: solid;
        box-sizing: border-box;
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
        display: inline-block;
    }
    .select-area-active {
        outline-color: none;
    }
    .select-area-caret {
        position: absolute;
        right: 0px;
        top: 0px;
        color: #cad8df;
        height: 100%;
        background: white;
        border-width: 1px 1px 1px 0;
        border-color: #cad8df;
        border-style: solid;
        box-sizing: border-box;
    }
    .select-area-up i {
        animation: sa-up 0.2s linear forwards;
    }
    .select-area-down i {
        animation: sa-down 0.2s linear forwards;
    }
    @keyframes sa-up {
        from {
                transform: rotate(0deg);
            } to {
                transform: rotate(180deg);
            }
    }
    @keyframes sa-down {
        from {
                transform: rotate(180deg);
            } to {
                transform: rotate(360deg);
            }
    }
    .sa-dropDown-menu {
        width: 100%;
        min-height: 35px;
        border: 1px solid #e4e7ed;
        border-radius: 3px;
        background: #fff;
        position: absolute;
        top: 45px;
        z-index: 10;
        padding: 5px 0;
        box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.1);
    }
    .sa-dropDown-menu ul {
        list-style: none;
        padding: 0;
        cursor: pointer;
        margin: 0;
    }
    .sa-dropDown-menu ul > li{
        padding: 5px 10px;
    }
    .sa-dropDown-menu ul > li:hover{
        background: #f1f1f1;
    }
</style>


