import Router from 'vue-router'
import login from '../components/login'
import {isAuth} from '../assets/js/toekn'

const mapRoute = route => {
    if (route.requireAuth !== false) {
        route.meta = Object.assign({}, route.meta, {requireAuth: true})
    }
    return route
}

const homePage = {name: 'User'}

const routes = [
    {
        path: '/login',
        name: 'login',
        component: login,
        requireAuth: false
    }, {
        path: '/',
        redirect: homePage
    }, {
        path: '/home',
        name: 'home',
        component: () => import('@/components/home'),
        children: [{
            path: 'user',
            name: 'User',
            component: () => import('@/components/user/user')
        }, {
            path: 'homework',
            name: 'homework',
            component: () => import('@/components/homework/homework')
        }, {
            path: 'group',
            name: 'group',
            component: () => import('@/components/group/group')
        }, {
            path: 'student',
            name: 'student',
            component: () => import('@/components/user/student')
        }, {
            path: 'teacher',
            name: 'teacher',
            component: () => import('@/components/user/teacher')
        }, {
            path: 'class',
            name: 'class',
            component: () => import('@/components/class/class')
        }, {
            path: 'collage',
            name: 'collage',
            component: () => import('@/components/collage/collage')
        }, {
            path: 'parameter',
            name: 'parameter',
            component: () => import('@/components/parameter/parameter')
        }, {
            path: 'system',
            name: 'system',
            component: () => import('@/components/system/system')
        }, {
            path: 'operationLog',
            name: 'operationLog',
            component: () => import('@/components/operationLog/operationLog')
        }, {
            path: 'netdisk',
            name: 'netdisk',
            component: () => import('@/components/netdisk/netdisk')
        }, {
            path: 'myClass',
            name: 'myClass',
            component: () => import('@/components/myClass/myClass')
        }, {
            path: 'apply',
            name: 'apply',
            component: () => import('@/components/apply/apply')
        }]
    }, {
        path: '*',
        redirect: homePage
    }
].map(mapRoute)

let router = new Router({
    routes
})

router.beforeEach((to, from, next) => {
    // already login
    if (to.name === 'login' && isAuth()) {
        next(false)
        return
    }

    // when unauthorized
    if (to.matched.some(route => route.meta.requireAuth)) {
        if (isAuth()) {
            next()
        } else {
            next({name: 'login'})
        }
    } else {
        next()
    }
})

export default router



