import {LOGIN, LOGOUT, OPENNAV, CHANGENAV, ROLETYPE} from './mutations_type'

export default {
    [LOGIN](state, user) {
        state.loginUser = user
    },
    [LOGOUT](state) {
        state.loginUser = null
    },
    [CHANGENAV](state, status) {
        state.navStatus = status
    },
    [ROLETYPE](state, type) {
        state.roleType = type
        if(type === 1) {
            state.isAdmin = true
        }
        if(type === 2) {
            state.isTeacher = true
        }
        if(type === 3) {
            state.isStudent = true
        }
    }
}



