import axios from 'axios'
import httpMethods from './httpMethods'
if(process.env.NODE_ENV === "production") {
    axios.defaults.baseURL = 'http://' + location.host + '/athena'
}

export default (Vue, config = {}) => {
    let defaultToken
    if ((defaultToken = config.defaultToken)) {
        delete config.defaultToken
    }
    let http = axios.create(config)
    const setDefaultHeader = (item, value) => {
        http.defaults.headers.common[item] = value
    }
    http.setDefaultToken = token => setDefaultHeader('Authorization', token)

    if (defaultToken) {
        http.setDefaultToken(defaultToken)
    }
    Object.defineProperty(Vue.prototype, '$http', {
        get () {
            return http
        }
    })
    Object.defineProperty(Vue.prototype, '$httpMethods', {
        get () {
            return (http => httpMethods.get(http))(http)
        }
    })
}



