import Vue from 'vue'
import App from './App'
import Router from 'vue-router'
import router from './router/router'
import VueI18n from 'vue-i18n'
import VueForm from 'vue-form'
import store from './vuex/store'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'font-awesome/css/font-awesome.css'
import {getToken, removeToken} from './assets/js/toekn'
import http from './assets/js/http'
import {CONFLICT_STATUS, UNAUTHORIZED_STATUS, FORBIDDEN_STATUS} from './assets/js/constant'
import '@/assets/css/app.css'


Vue.config.productionTip = false
Vue.use(VueI18n)
Vue.use(VueForm)
Vue.use(ElementUI)
Vue.use(Router)
Vue.use(http, {
    defaultToken: getToken(),
    headers: {'content-type': 'application/json'}
})

Vue.prototype.globalClick = callback => {
    document.getElementById('app').addEventListener('click',() => callback())
}
let ff = 'zh'
const i18n = new VueI18n({
    locale: 'zh',
    messages: {
        zh: require(`./assets/i18n/${ff}`)
    }
})

let vue = new Vue({
    el: '#app',
    router,
    i18n,
    store,
    components: {App},
    template: '<App/>'
})


vue.$http.interceptors.response.use(
    null,
    error => {
        let response = error.response
        // 409 conflict
        if (response.status === CONFLICT_STATUS) {
            vue.$notify({
                type: 'error',
                title: vue.$t('error.common'),
                message: response.data.message
            })
        } else if (response.status === UNAUTHORIZED_STATUS || response.status === FORBIDDEN_STATUS) {
            // token unauthorized
            removeToken()
            router.push({name: 'login', query: router.currentRoute.name})
        }
        return error
    })



